var searchKey = "lang";
var localStorageKey = "ffxiv-crafter-language";
export function useLanguage() {
    var language = new URLSearchParams(window.location.search.substring(1)).get(searchKey);
    if (!language) {
        language = localStorage.getItem(localStorageKey);
    }
    if (!language) {
        language = "en";
    }
    function setLanguage(newLanguage) {
        localStorage.setItem(localStorageKey, newLanguage);
        location.search = "?lang=".concat(newLanguage);
    }
    return [language, setLanguage];
}
