import { SupportedMethod } from "./const";
function run(message) {
    var worker = new Worker("./worker.js");
    return new Promise(function (resolve, reject) {
        worker.onmessage = function (event) {
            if (event.data === "worker ready") {
                worker.postMessage(message);
                return;
            }
            worker.terminate();
            resolve(event.data);
        };
        worker.onerror = function (event) {
            worker.terminate();
            reject(event);
        };
        worker.onmessageerror = function () {
            worker.terminate();
        };
    });
}
export function plan_macro(params, initial_quality, longer) {
    return run([SupportedMethod.PLAN_MACRO, params, initial_quality, longer]);
}
export function search_best_move(params, state) {
    return run([SupportedMethod.SEARCH_BEST_MOVE, params, state]);
}
