import * as wasm from "ffxiv-crafter-rust";
export function search_best_move(params, state) {
    return JSON.parse(wasm.search_best_move(JSON.stringify(params), JSON.stringify(state)));
}
export function apply_action(params, state, action) {
    var serialized_states = wasm.apply_action(JSON.stringify(params), JSON.stringify(state), action);
    return JSON.parse(serialized_states);
}
export function play_action(params, state, action) {
    var serialized_states = wasm.play_action(JSON.stringify(params), JSON.stringify(state), action);
    return JSON.parse(serialized_states);
}
export function available_actions(params, state) {
    var available_actions_str = wasm.available_actions(JSON.stringify(params), JSON.stringify(state));
    return JSON.parse(available_actions_str);
}
export function plan_macro(params, initial_quality, longer) {
    var actions_str = wasm.plan_macro(JSON.stringify(params), BigInt(initial_quality), longer);
    return JSON.parse(actions_str);
}
export function evaluate_macro(params, actions, initial_quality) {
    var metrics_str = wasm.evaluate_macro(JSON.stringify(params), JSON.stringify(actions), BigInt(initial_quality));
    return JSON.parse(metrics_str);
}
